<template>
	<swiper
		:indicator-dots="true"
		:autoplay="true" 
		:interval="3000" 
		:duration="1000" 
		circular
		:current="current"
		@change="onChange"
		>	
		<!-- 绑定current，代表当前swiper,监听change事件，current改变会触发change事件，event.derail={current:current,source:source} -->
			<swiper-item 
			v-for="(item,index) in info"
			:key="index">
				<image :src="item.url" mode="scaleToFill"></image>
				<view class="title">
					{{ info[current].title }}
				</view>
			</swiper-item>
	</swiper>
</template>

<script>
	
	export default{
		props:{
			info:Array,
			current:{
				type:Number,
				default:0
			},
		},
		methods:{
			onChange(){
				this.$emit('change')
			}
		}
	}
</script>

